import { v4 as uuidv4 } from 'uuid';
import { SET_EXERCISES_ACTIVE_LIST, UPDATE_ACTIVE_LIST, SET_ACTIVE_EXERCISE, CLEAR_EXERCISES } from '../actions/exercises';

const initialState = {
  list: [
    {
      day: 1, // 
      exercises: [
        {
          id: uuidv4(),
          name: '  ',
          loads: [
            {
              id: uuidv4(),
              date: '13.11.2022',
              value: '7.5 ',
              status: 'up'
            }
          ],
          currentLoadValue: 7.5
        },
        {
          id: uuidv4(),
          name: '  ',
          loads: [],
          currentLoadValue: 7.5
        },
        {
          id: uuidv4(),
          name: '  ',
          loads: [],
          currentLoadValue: 7.5
        },
        {
          id: uuidv4(),
          name: '  ',
          loads: [],
          currentLoadValue: 7.5
        },
        {
          id: uuidv4(),
          name: '   ',
          loads: [],
          currentLoadValue: 7.5
        },
        {
          id: uuidv4(),
          name: '',
          loads: [],
          currentLoadValue: 7.5
        }
      ]
    },
    {
      day: 3, // 
      exercises: [
        {
          id: uuidv4(),
          name: '  ',
          loads: [],
          currentLoadValue: 7.5
        },
        {
          id: uuidv4(),
          name: '  ',
          loads: [],
          currentLoadValue: 7.5
        }
      ]
    },
    {
      day: 5, // 
      exercises: [
        {
          id: uuidv4(),
          name: '',
          loads: [],
          currentLoadValue: 7.5
        },
        {
          id: uuidv4(),
          name: ' ',
          loads: [],
          currentLoadValue: 7.5
        },
        {
          id: uuidv4(),
          name: '',
          loads: [],
          currentLoadValue: 7.5
        },
        {
          id: uuidv4(),
          name: '  ',
          loads: [],
          currentLoadValue: 7.5
        }
      ]
    }
  ], //   
  activeList: [], //       
  active: '' //  
};

export const exercisesReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case SET_EXERCISES_ACTIVE_LIST:
      return {
        ...state,
        activeList: state.list.find((item) => item.day === payload).exercises.map((exercise) => exercise.name)
      };

    case UPDATE_ACTIVE_LIST:
      return {
        ...state,
        activeList: state.activeList.slice(1)
      };

    case SET_ACTIVE_EXERCISE:
      return {
        ...state,
        active: state.activeList[0]
      };

    case CLEAR_EXERCISES:
      return {
        ...state,
        activeList: [],
        active: ''
      };

    default:
      return state;
  }
};
